import React from 'react';
import ReactDOM from 'react-dom';
var EventEmitter = require('eventemitter3');



var createReactClass = require('create-react-class');
var calories;
window.ee = new EventEmitter();

var Add = createReactClass({
  getInitialState: function () {
    return {
      weigthIsEmpty: true,
      ageIsEmpty: true,
      heigthIsEmpty: true
    };
  },

  componentDidMount: function () {
    ReactDOM.findDOMNode(this.refs.weigth).focus();
  },

  onBtnClickHandler: function (e) {
    e.preventDefault();

    var weigth = ReactDOM.findDOMNode(this.refs.weigth).value;
    var age = ReactDOM.findDOMNode(this.refs.age).value;
    var heigth = ReactDOM.findDOMNode(this.refs.heigth).value;

    //=((9,99*weigth+6,25*heigth-4,92*age-161)*1,375)*0,85 - formula

    calories = ((9.99 * weigth + 6.25 * heigth - 4.92 * age - 161) * 1.375)* 0.85;
    calories = Math.round(calories);
    this.setState({ textIsEmpty: true });
    window.ee.emit('Input_finished', calories);
  },

  onFieldChange: function(fieldName, event) {
    if (event.target.value.trim().length > 0) {
      this.setState({ ['' + fieldName]: false })
    } else {
      this.setState({ ['' + fieldName]: true })
    }
  },

  render: function() {
    var weigthIsEmpty = this.state.agreeNotChecked,
      ageIsEmpty = this.state.authorIsEmpty,
      heigthIsEmpty = this.state.textIsEmpty;
    return (
      <div className="temp">
        <div className="columns is-centered is-mobile test">

            <div className="field column is-one-fifth-desktop is-one-third-mobile">
              <label className="label has-text-centered"></label>
              <div className="control">
                <input
                  type='text'
                  className='add__weigth input'
                  onChange={this.onFieldChange.bind(this, 'weigthIsEmpty')}
                  placeholder=' '
                  ref='weigth'
                />
              </div>
            </div>
            <div className="field column is-one-fifth-desktop  is-one-third-mobile">
              <label className="label has-text-centered"></label>
              <div className="control">
                <input
                  type='text'
                  className='add__age input'
                  onChange={this.onFieldChange.bind(this, 'ageIsEmpty')}
                  placeholder=' '
                  ref='age'
                />
              </div>
            </div>
            <div className="field column is-one-fifth-desktop  is-one-third-mobile">
              <label className="label has-text-centered"></label>
              <div className="control">
                <input
                  type='text'
                  className='add__heigth input '
                  onChange={this.onFieldChange.bind(this, 'heigthIsEmpty')}
                  placeholder=' '
                  ref='heigth'
                />
              </div>
            </div>
          </div>

          <div className="has-text-centered">
            <button
              type="button"
              className='add__btn button is-primary has-text-centered is-medium'
              onClick={this.onBtnClickHandler}
              ref='calc_button'
              >
               
            </button>
          </div>

      </div>
    );
  }
});



var Output = createReactClass({
  getInitialState: function () {
    return {
      output_calories: "",
      carbs: "",
      proteins: "",
      fat: ""
    };
  },
  componentDidMount: function () {
    var self = this;
    window.ee.addListener('Input_finished', function (calories) {
      self.setState({
        output_calories: calories,
        carbs: Math.round(calories * 0.1),
        proteins: Math.round(calories / 16),
        fat: Math.round(calories * 0.35 / 9)
         })
    });
  },
  componentWillUnmount: function () {
    window.ee.removeListener('Input_finished');
  },
  render() {
    return (
          <table  className="table is-striped is-size-6	center-table">
            <tbody>

              <tr>
                <td>  : </td>
                <td><span>{this.state.output_calories}</span></td>
              </tr>

              <tr>
                <td>  : </td>
                <td><span>{this.state.proteins}</span></td>
              </tr>

              <tr>
                <td>  : </td>
                <td><span>{this.state.fat}</span></td>
              </tr>

              <tr>
                <td>  : </td>
                <td><span>{this.state.carbs}</span></td>
              </tr>

            </tbody>
          </table>

    );
  }
})



ReactDOM.render(
  <div>
    <Add />
    <Output />
  </div>,
  document.getElementById('root')
);



